<template lang="html">
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title"></h1>
        <div class="close" @click="hide">
          <i class="icon iconfont icon-shachu-xue"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="searchBox" @query="onQueryChange" placeholder=""></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
        <div class="list-wrapper">
          <scroll ref="songList" class="list-scroll" v-if="currentIndex===0" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
          <scroll ref="searchList" :refreshDelay="refreshDelay" class="list-scroll" v-if="currentIndex===1" :data="searchHistory">
            <div class="list-inner">
              <search-list @delete="deleteSearchHistory" @select="addQuery" :searches="searchHistory"></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest :query="query" :showSinger="showSinger" @select="selectSuggest" @listScroll="blurInput"></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script>
  import SearchBox from 'base/search-box/search-box'
  import Suggest from 'components/suggest/suggest'
  import {searchMixin} from 'common/js/mixin'
  import Switches from 'base/switches/switches'
  import Scroll from 'base/scroll/scroll'
  import {mapGetters,mapActions} from 'vuex'
  import SongList from 'base/song-list/song-list'
  import SearchList from 'base/search-list/search-list'
  import Song from 'common/js/song'
  import TopTip from 'base/top-tip/top-tip'
  export default {
    mixins: [searchMixin],
    data() {
      return {
        showFlag : false,
        showSinger : false,
        currentIndex : 0,
        switches: [
          {name : ''},
          {name : ''}
        ]
      }
    },
    computed: {
      ...mapGetters([
        'playHistory'
      ])
    },
    methods: {
      show() {
        this.showFlag = true
        setTimeout(() => {
          if (this.currentIndex === 0) {
            this.$refs.songList.refresh()
          } else {
            this.$refs.searchList.refresh()
          }
        },20)
      },
      hide() {
        this.showFlag = false
      },
      selectSuggest() {
        this.saveSearch()
        this.showTip()
      },
      switchItem(index) {
        this.currentIndex = index
      },
      selectSong(song, index) {
        if (index!==0) {
          this.insertSong(new Song(song))
          this.showTip()
        }
      },
      showTip() {
        this.$refs.topTip.show()
      },
      ...mapActions([
        'insertSong'
      ])
    },
    components: {
      SearchBox,
      Suggest,
      Switches,
      Scroll,
      SongList,
      SearchList,
      TopTip
    }
  }

</script>

<style lang="css" scoped="">
  .add-song{
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 200;
    background: #222;
  }
  .slide-enter-active, .slide-leave-active{
    transition: all 0.3s;
  }
  .slide-enter, .slide-leave-to{
    transform: translate3d(100%, 0, 0);
  }
  .add-song .header{
    position: relative;
    height: 44px;
    text-align: center;
  }
  .add-song .header .title{
    line-height: 44px;
    font-size: 18px;
    color: #fff;
  }
  .add-song .header .close{
    position: absolute;
    top: 0;
    right: 8px;
  }
  .icon-shachu-xue{
    display: block;
    padding: 12px;
    font-size: 20px;
    color: #fb7299;
  }
  .search-box-wrapper{
    margin: 20px;
  }
  .search-result{
    position: fixed;
    top: 124px;
    bottom: 0;
    width: 100%;
  }
  .shortcut .list-wrapper{
    position: absolute;
    top: 165px;
    bottom: 0;
    width: 100%;
  }
  .shortcut .list-wrapper .list-scroll{
    height: 100%;
    overflow: hidden;
  }
  .shortcut .list-wrapper .list-scroll .list-inner{
    padding: 20px 30px;
  }
  .tip-title{
    text-align: center;
    padding: 18px 0;
    font-size: 0;
  }
  .icon-ok{
    font-size: 14px;
    color: #fb7299;
    margin-right: 4px;
  }
  .tip-title .text{
    font-size: 14px;
    color: #fff;
  }




</style>



